import React from "react"
import Clipboard from 'clipboard'
import Ink from 'react-ink'

const tabs = ['点餐', '已点'];

const menus = ["大鸡腿", "全翅", "翅中", "鸡尖", "秋刀鱼", "大烤肠", "肥肠串", "五花肉", "大热狗",
  "米香鸡块", "米香鸡排", "大里脊", "鸡胗", "60克双汇王中王", "肥牛", "牛板筋", "纯肉排", "QQ大鸡排",
  "大鱿鱼", "鱿鱼须", "螺蛳肉", "牛肉串", "羊肉串", "猪脆骨", "掌中宝", "大鸡柳", "贡丸", "鱼豆腐",
  "牛肚", "排骨串", "大肉串", "大骨连",
  "奶香馒头", "小茄子", "肉沫蒜香大茄子", "花菜", "韭菜", "雪榕牌金针菇", "娃娃菜", "玉米", "小青菜",
  "土豆片", "香干", "素鸡", "年糕", "藕片", "千叶豆腐", "香菇", "老豆腐", "面筋", "黄金面包片", "南瓜饼",
  "四季豆", "臭豆腐", "黄金糕", "火烧饼", "紫薯饼",
  "小热狗", "牛蹄筋", "油豆腐"
];

class Comp extends React.Component {
  constructor(props) {
    super(props);
    this.setTab = this.setTab.bind(this);
    this.increment = this.increment.bind(this);
    this.decrement = this.decrement.bind(this);
    this.state = {
      menus: menus.slice(),
      order: {},
      tabs: tabs.slice(),
      activeTab: tabs[0]
    }
  }

  componentDidMount() {
    this._cp = new Clipboard('.copy-menus', {
      text: () => {
        const {order} = this.state;
        return Object.entries(order).filter(([k, v]) => v > 0).map(([k, v]) => k + ': ' + v).join('\n');
      }
    })
  }

  componentWillUnmount() {
    this._cp && this._cp.destroy();
  }

  setTab(e) {
    this.setState({activeTab: e.currentTarget.dataset.tab})
  }

  increment(e) {
    const {menu} = e.currentTarget.dataset;
    const {order} = this.state;
    this.setState({
      order: Object.assign({}, order, {[menu]: (order[menu] || 0) + 1})
    })
  }

  decrement(e) {
    const {menu} = e.currentTarget.dataset;
    const {order} = this.state;
    if (!order[menu]) {
      return;
    }
    this.setState({
      order: Object.assign({}, order, {[menu]: (order[menu] || 0) - 1})
    })
  }

  resolveMenus() {
    const {activeTab, menus, order} = this.state;
    if (activeTab === '点餐') {
      return menus;
    }
    return Object.entries(order).reduce((p, [k, v]) => {
      return v === 0 ? p : (p.push(k), p);
    }, [])
  }

  buildMenus(menus) {
    if (!menus.length) {
      return <div>无菜单</div>
    }

    const {order} = this.state;
    const size = 6;

    return Array.from({length: Math.ceil(menus.length / size)})
      .map((_, i) => menus.slice(i * size, (i + 1) * size))
      .map(menus => {
        return <div className="columns">
          {
            menus.map((menu, i) => {
              return <Col key={menu}>
                <Card idx={i} menu={menu} num={order[menu] || 0} increment={this.increment} decrement={this.decrement}/>
              </Col>
            })
          }
        </div>
      })

  }

  render() {
    const {activeTab} = this.state;
    const menus = this.resolveMenus();
    const hidden = activeTab === '已点' && menus.length ? '' : 'hidden';

    return <div className='container'>
      <div className="tabs">
        <ul>
          {tabs.map(tab => {
            return <li key={tab} className={activeTab === tab ? "ink is-active" : 'ink'} data-tab={tab}
                       onClick={this.setTab}><a>{tab}</a><Ink/></li>
          })}
        </ul>
      </div>
      <button className={"button is-primary is-small copy-menus ink " + hidden}>复制菜单<Ink/></button>
      {
        this.buildMenus(menus)
      }

    </div>
  }
}

const Card = ({menu, num, increment, decrement, idx}) => {
  return <div className="card">
    <div className="card-content">
      <div className="media">
        <div className="media-content">
          <p className="title is-6">{menu}</p>
          <p className="subtitle is-7">库存:有</p>
        </div>
      </div>

      <div className="content has-text-right">
        <span className="icon ink" onClick={decrement} data-menu={menu}><i className="fa fa-minus-square"/><Ink/></span>
        <span className='has-text-danger'>{num}</span>
        <span className="icon ink" onClick={increment} data-menu={menu}><i className="fa fa-plus-square"/><Ink/></span>
        <br/>
      </div>
    </div>
  </div>
};

const Col = (props) => {
  return <div className="column is-2">{props.children}</div>
};


export default Comp;
